import Vue from 'vue';

export default {
  props: {
    food: {
      type: Object
    }
  },
  created() {},
  methods: {
    addCart(e) {
      if (!this.food.count) {
        Vue.set(this.food, 'count', 1);
      } else {
        this.food.count ++;
      }
      this.$emit('add-cart', e.target);
      e.stopPropagation();
      e.preventDefault();
    },
    decreaseCart(e) {
      this.food.count --;
      e.stopPropagation();
      e.preventDefault();
    }
  }

};



