import Star from 'components/star/star.vue';
import Split from 'components/split/split.vue';
import Ratingselect from 'components/ratingselect/ratingselect.vue';
import BScroll from 'better-scroll';
const POSITIVE = 0;
const NEGATIVE = 1;
const ERR_OK = 0;
const ALL = 2;
export default {
  data() {
    return {
      showFlag: false,
      onlyContent: false,
      selectType: ALL,
      ratings: []
    };
  },
  props: {
    seller: {
      type: Object
    }
  },
  created() {
    this.$http.get('/api/ratings').then((res) => {
      res = res.body;
      if (res.errno === ERR_OK) {
        this.ratings = res.data;
        this.refreshBScroll();
      }
    });
  },
  components: { Star, Split, Ratingselect },
  methods: {
    refreshBScroll() {
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.ratings, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    changeType(type) {
      console.log(type);
      this.selectType = type;
      this.refreshBScroll();
    },
    toggleContent(bool) {
      this.onlyContent = bool;
      this.refreshBScroll();
    },
    format(time) {
      return this.formatDate(time, 'YYYY-MM-DD hh:mm:ss');
    },
    needShow(type, text) {
      if (this.onlyContent && !text) {
        return false;
      }
      if (this.selectType === ALL) {
        return true;
      } else {
        return this.selectType === type;
      }
    }
  }
};



