export default {
  props: {
    supports: {
      type: Array,
      default() {
        return [];
      }
    },
    supportsClass: {
      type: String,
      default: 'support'
    }
  },
  created() {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
  },
  methods: {}
};



