/*   */
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;

export default {
  props: {
    ratings: {
      type: Array,
      default() {
        return [];
      }
    },
    onlyContent: {
      type: Boolean,
      default: false
    },
    selectType: {
      type: Number,
      default: ALL
    },
    desc: {
      type: Object,
      default() {
        return {
          all: '全部',
          positive: '满意',
          negative: '不满意'
        };
      }
    },
    title: {
      type: String
    }
  },
  computed: {
    positives() {
      return this.ratings.filter((rating) => {
        return rating.rateType === POSITIVE;
      });
    },
    negatives() {
      return this.ratings.filter((rating) => {
        return rating.rateType === NEGATIVE;
      });
    }
  },
  methods: {
    select(type, e) {
      // this.selectType = type;
      this.$emit('rating-type-change', type);
      e.preventDefault();
      e.stopPropagation();
    },
    toggleContent(e) {
      // this.onlyContent = !this.onlyContent;
      this.$emit('toggle-content', !this.onlyContent);
      e.preventDefault();
      e.stopPropagation();
    }
  }
};



