const ERR_OK = 0;
import BScroll from 'better-scroll';
import Carcontrol from 'components/carcontrol/carcontrol.vue';
import Shopcar from 'components/shopcar/shopcar.vue';
import Food from 'components/food/food.vue';
export default {
  data() {
    return {
      goods: [],
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    };
  },
  props: {
    seller: {
      type: Object
    }
  },
  created() {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    this.$http.get('/api/goods').then((res) => {
      res = res.body;
      if (res.errno === ERR_OK) {
        this.goods = res.data;
        this.$nextTick(() => {
          this._initScroll();
          this._caclulateHeight();
        });
      };
    });
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            return i;
          }
      }
    },
    selectFoods() {
      let foods = [];
      this.goods.map((good) => {
        good.foods.map((food) => {
          if (food.count) {
            foods.push(food);
          }
        });
      });
      return foods;
    }
  },
  components: { Carcontrol, Shopcar, Food },
  methods: {
    _initScroll() {
      this.menuScroll = new BScroll(this.$refs['menu-wrapper'], {
        click: true
      });
      this.foodScroll = new BScroll(this.$refs['food-wrapper'], {
        probeType: 3,
        click: true
      });

      this.foodScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y));
      });
    },
    _caclulateHeight() {
      let foodList = this.$refs['food-wrapper'].getElementsByClassName('food-list-hook');
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    selectMenu(index) {
      let foodList = this.$refs['food-wrapper'].getElementsByClassName('food-list-hook');
      let el = foodList[index];
      this.foodScroll.scrollToElement(el, 500);
    },
    cartAdd(target) {
      this.$refs.shopcar._drop(target);
    },
    selectFood(food, e) {
      e.stopPropagation();
      this.selectedFood = food;
      this.$refs.food.show();
    }
  }
};



