import BScroll from 'better-scroll';
import Carcontrol from 'components/carcontrol/carcontrol.vue';
import Vue from 'vue';
import Split from 'components/split/split.vue';
import Ratingselect from 'components/ratingselect/ratingselect.vue';
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;

export default {
  data() {
    return {
      showFlag: false,
      onlyContent: false,
      selectType: ALL,
      desc: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽'
      }
    };
  },
  props: {
    food: {
      type: Object
    }
  },
  components: {Carcontrol, Split, Ratingselect},
  methods: {
    show() {
      this.showFlag = true;
      this.onlyContent = true;
      this.refreshBScroll();
    },
    refreshBScroll() {
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    hide() {
      this.showFlag = false;
    },
    addFirst(e) {
      Vue.set(this.food, 'count', 1);
      this.$emit('add-cart', e.target);
    },
    cartAdd(el) {
      this.$emit('add-cart', el);
    },
    changeType(type) {
      this.selectType = type;
      this.refreshBScroll();
    },
    toggleContent(bool) {
      this.onlyContent = bool;
      this.refreshBScroll();
    },
    needShow(type, text) {
      if (this.onlyContent && !text) {
        return false;
      }
      if (this.selectType === ALL) {
        return true;
      } else {
        return this.selectType === type;
      }
    },
    formatData (time) {
      return this.formatDate(time, 'YYYY-MM-DD hh:mm:ss');
    }
  }
};



