import BScroll from 'better-scroll';
import Star from 'components/star/star.vue';
import Split from 'components/split/split.vue';
import Supports from 'components/supports/supports.vue';
export default {
  data() {
    return {
      favorite: false
    };
  },
  props: {
    seller: {
      type: Object,
      default: {}
    }
  },
  created() {
    let favorite = this.loadFromLoacl(this.seller.id, 'favorite', false);
    this.favorite = favorite;
    this._refreshBScroll();
    this._initPics();
  },
  components: {Star, Split, Supports},
  watch: {
    'seller'() {
      this._refreshBScroll();
      this._initPics();
    }
  },
  computed: {
    favoriteText() {
      if (this.favorite) {
        return '已收藏';
      }
      return '点击收藏';
    }
  },
  methods: {
    _refreshBScroll() {
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.seller, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    _initPics() {
      if (!this.seller.pics) { return; };
      if (!this.picScroll) {
        this.$nextTick(() => {
          let picWidth = 120;
          let margin = 6;
          let width = (picWidth + margin) * this.seller.pics.length - margin;
          this.$refs['pic-list'].style.width = width + 'px';
          this.picScroll = new BScroll(this.$refs['pic-wrapper'], {
            scrollX: true,
            eventPassthrough: 'vertical'
          });
        });
      } else {
        this.picScroll.refresh();
      }
    },
    _toggleFavorite() {
      this.favorite = !this.favorite;
      this.saveToLoacl(this.seller.id, 'favorite', this.favorite);
    }

  }
};



